import { createMuiTheme } from 'material-ui/styles';
import indigo from 'material-ui/colors/indigo';
import deepOrange from 'material-ui/colors/deepOrange';

export default createMuiTheme({
  palette: {
    primary: indigo,
    secondary: deepOrange,
    type: 'light'
  }
});



